<template>
        <div id="add-blog">
            <h2 v-if="!submitted">编辑你的博客(^_^)</h2>
    
            <form v-if="!submitted">
                <label>博客标题</label>
                <input type="text" v-model="blog.title" required />
    
                <label>博客内容</label>
                <textarea v-model="blog.content"></textarea>
    
                <div id="checkboxes">
                    <label>工作</label>
                    <input type="checkbox" value="工作" v-model="blog.categories">
                    <label>学习</label>
                    <input type="checkbox" value="学习" v-model="blog.categories">
                    <label>生活</label>
                    <input type="checkbox" value="生活" v-model="blog.categories">
                    <label>沙雕</label>
                    <input type="checkbox" value="沙雕" v-model="blog.categories">
                </div>
    
                <label>作者</label>
                <select v-model="blog.author">
                    <option v-for="author in authors">
                        {{author}}
                    </option>
                </select>
    
                <button v-on:click.prevent="post">确认</button>
            </form>
    
            <div v-if="submitted">
                <h3>发布成功~</h3>
            </div>
        </div>
    </template>
    
    <script>
        export default {
            name: 'add-blog',
            data() {
                return {
                    id:this.$route.params.id,
                    blog: {},
                    authors: ["Gintoki", "Kagura", "Shinbachi","梁港平daughter"],
                    submitted:false
                }
            },
            methods:{
                fetchData(){
                    // console.log(this.id);
                    this.$http.get("https://vuemyblog.firebaseio.com/posts/" + this.id + ".json")
                    .then(response =>{
                        // console.log(response.body);
                        this.blog = response.body;
                    })
                },
                post:function(){
                    this.$http.put("https://vuemyblog.firebaseio.com/posts/" + this.id + ".json",this.blog)
                    .then(function(data){
                        console.log(data);
                        this.submitted = true;
                    });
                }
            },
            created(){
                this.fetchData();
            }
        }
    </script>
    
    <!-- Add "scoped" attribute to limit CSS to this component only -->
    <style scoped>
    #add-blog *{
        box-sizing: border-box;
    }
    
    #add-blog{
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
        background: #F0E7DA;
    }
    
    label{
        display: block;
        margin: 20px 0 10px;
    }
    
    input[type = "text"],textarea,select{
        display: block;
        width: 100%;
        padding: 8px;
    }
    
    textarea{
        height: 200px;
    }
    
    #checkboxes label{
        display: inline-block;
        margin-top: 5px;
    }
    
    #checkboxes input{
        display: inline-block;
        margin-right: 10px;
    }
    
    button{
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        border: 0;
        padding: 14px;
        border-radius: 4px;
        font-size: 18px; 
        cursor: pointer;
    }
    
    </style>


