<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <p>分类</p>
        <ul>
            <li v-for="category in blog.categories">
                {{category}}
            </li>
        </ul>
        <router-link v-bind:to="'/edit/' + id">编辑</router-link>
        <button v-on:click="deleteSingleBlog()">删除</button>
    </div>
</template>

<script>
    export default{
        name:"single-blog",
        data(){
            return {
                id:this.$route.params.id,
                blog:{}
            }
        },
        created(){
            this.$http.get("https://vuemyblog.firebaseio.com/posts/" + this.id + ".json")
            .then(function(data){
                return data.json();
                // console.log(data);
                // this.blog = data.body;
            })
            .then(function(data){
                this.blog = data;
            })
        },
        methods:{
            deleteSingleBlog(){
                this.$http.delete("https://vuemyblog.firebaseio.com/posts/" + this.id + ".json")
                .then(response =>{
                    this.$router.push({path:'/'})
                })
            }
        }
    }
</script>

<style scoped>
    #single-blog{
        max-width: 960px;
        background: #F0E7DA;
        margin: 0 auto;
        padding: 20px;
        border: 1px dotted #aaa;
    }

    button{
        display: inline-block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        border: 0;
        padding: 14px;
        border-radius: 4px;
        font-size: 18px; 
        cursor: pointer;
    }

    a{
        color: rgb(220,20,60);
        text-decoration: none;
        padding: 16px;
        border-radius: 4px;
        background: rgba(255,255,255,0.8);
    }
</style>


