<template>
    <nav>
        <ul>
            <li>
                <router-link to="/" exact>主页</router-link>
                <router-link to="/add" exact>添加</router-link>
            </li>
        </ul>
    </nav>
</template>

<script>
    export default{
        name:"blog-header"
    }
</script>

<style scoped>
    ul{
        list-style-type: none;
        text-align: center;
        margin: 0;
    }

    li{
        display: inline-block;
        margin: 0 10px;
    }

    a{
        color: #fff;
        text-decoration: none;
        padding: 12px;
        border-radius: 4px;
    }

    nav{
        background: #E5CBC1;
        padding: 30px 0;
        margin-bottom: 30px;
    }

    .router-link-active{
        background: rgba(255,255,255,0.8);
        color: #444;
    }
</style>


